<template>
  <div class="view" v-if="url">
    <div class="pdf">
      <pdf
        ref="pdf"
        :src="url"
        v-for="index in numPages"
        :key="index"
        :page="index"
        @page-loaded="pageLoaded($event)"
      ></pdf>
    </div>
  </div>
</template>

<script>
import pdf from "vue-pdf";
export default {
  name: "PdfView",
  components: { pdf },
  props: {
    url: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      toast: null,
      show: false,
      hasLoaded: false,
      loadMessage: "...",
      numPages: null, // pdf 
      curPageNum: 0,
    };
  },
  watch: {
    curPageNum: {
      handler: function (val) {
        if (this.numPages == val) {
          this.toast.clear(); //    toast
        }
      },
    },
  },
  methods: {
    //  pdf 
    pageLoaded(num) {
      // num  
      this.curPageNum = num;
    },
    // pdf(pdf)
    getNumPages() {
      this.hasLoaded = false;
      this.toast = this.$toast({
        type: "loading",
        loadingType: "spinner",
        duration: 0,
        overlay: true,
        message: this.loadMessage,
        forbidClick: true,
      });
      const loadingTask = pdf.createLoadingTask(this.url);
      loadingTask.promise
        .then((pdf) => {
          this.numPages = pdf.numPages;
          this.$nextTick(() => {
            this.hasLoaded = true;
          });
        })
        .catch((err) => {
          this.loadMessage = "";
          this.hasLoaded = false;
        });
    },
  },
  mounted() {
    if (!this.url) {
      this.$toast({
        type: "fail",
        duration: 0,
        message: " URL",
      });
      return;
    }
    this.getNumPages();
  },
};
</script>

